import { Injectable, Inject } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

import { Observable } from 'rxjs';
import { Department } from 'src/app/staff/services/departments/department';
import { DepartmentsFactory } from 'src/app/staff/services/departments/departments.factory';

@Injectable()
export class DepartmentsResolver implements Resolve<Department[]>  {
  constructor(@Inject('DepartmentsFactory') private departmentsFactory: DepartmentsFactory) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<Department[]> {
    return this.departmentsFactory.getItemsByParentId();
  }
}
